<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process136"></a>

<p><br></p>
<p><span class="rvts17">比死亡还要糟糕的命运（</span><span class="rvts50">A Fate Worse Than Death</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:610,Y:607</span><span class="rvts21">，敌人：死灵法师塔姆西（</span><span class="rvts20">Tamsyn</span><span class="rvts21">）、死灵法师雷思科（</span><span class="rvts20">Rask</span><span class="rvts21">）、死灵法师格卫科（</span><span class="rvts20">Gwick</span><span class="rvts21">）</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">干掉</span><span class="rvts20">3</span><span class="rvts21">位恶毒的死灵法师</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1073.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">该任务起始在黑暗洞穴，找到</span><span class="rvts20">3</span><span class="rvts21">处死灵法师的灵柩罐时（详见</span><span class="rvts20"></span><span class="rvts21">布拉克斯</span><span class="rvts20"></span><span class="rvts21">雷克斯藏宝室</span><span class="rvts20"></span><span class="rvts21">支线任务），就可以接到。然而，任务所对应的</span><span class="rvts20">3</span><span class="rvts21">个人是在</span><span class="rvts20"></span><span class="rvts21">神龙迷宫</span><span class="rvts20"></span><span class="rvts21">的隐匿塔内。</span>
<br><span class="rvts21">他们</span><span class="rvts20">3</span><span class="rvts21">个人中，有</span><span class="rvts20">1</span><span class="rvts21">个人拿着开启高塔深处大门的钥匙。如果不打碎他们的灵柩罐，他们将会在被击败</span><span class="rvts20">20</span><span class="rvts21">秒左右后重新复活。</span>
<br><span class="rvts52"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1074.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">烧毁或使用其它办法彻底击碎它们的灵柩罐，他们将会彻底死去，该任务将会关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



